* {
	margin: 0;
	padding: 0;
}

h1,
h2,
p {
	margin: 0;
	padding: 0;
}

body {
	padding-left: 40px;
	padding-right: 40px;
	overflow-x: hidden;
}

.select-container {
	margin-top: 20px;
}

.breed-select {
	width: 150px;
	margin-bottom: 10px;
	padding: 4px;
}

.box {
	display: flex;
	flex-direction: column;
	gap: 20px;
}

.cat-info {
	display: flex;
	gap: 30px;
}

.text-container {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	gap: 16px;
}

.title,
.text,
.subtitle {
	color: #22343d;
	font-family: sans-serif;
	font-size: 24px;
	font-weight: 700;
	line-height: normal;
}

.text {
	font-size: 16px;
}

.subtitle {
	font-size: 22px;
	font-weight: 600;
}

.loader {
	font-size: 18px;
	font-weight: 600;
}

.error {
	font-weight: 16px;
	font-weight: 500;
	color: red;
}

.is-hidden {
	display: none;
}

.ss-deselect {
    display: none;
}

.ss-arrow {
    display: none;
}

.loaders {
    transform: rotateZ(45deg);
    perspective: 1000px;
    border-radius: 50%;
    width: 48px;
    height: 48px;
    color: #fff;
  }
    .loaders:before,
    .loaders:after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: inherit;
      height: inherit;
      border-radius: 50%;
      transform: rotateX(70deg);
      animation: 1s spin linear infinite;
    }
    .loaders:after {
      color: #FF3D00;
      transform: rotateY(70deg);
      animation-delay: .4s;
    }

  @keyframes rotate {
    0% {
      transform: translate(-50%, -50%) rotateZ(0deg);
    }
    100% {
      transform: translate(-50%, -50%) rotateZ(360deg);
    }
  }

  @keyframes rotateccw {
    0% {
      transform: translate(-50%, -50%) rotate(0deg);
    }
    100% {
      transform: translate(-50%, -50%) rotate(-360deg);
    }
  }

  @keyframes spin {
    0%,
    100% {
      box-shadow: .2em 0px 0 0px currentcolor;
    }
    12% {
      box-shadow: .2em .2em 0 0 currentcolor;
    }
    25% {
      box-shadow: 0 .2em 0 0px currentcolor;
    }
    37% {
      box-shadow: -.2em .2em 0 0 currentcolor;
    }
    50% {
      box-shadow: -.2em 0 0 0 currentcolor;
    }
    62% {
      box-shadow: -.2em -.2em 0 0 currentcolor;
    }
    75% {
      box-shadow: 0px -.2em 0 0 currentcolor;
    }
    87% {
      box-shadow: .2em -.2em 0 0 currentcolor;
    }
  }
